import React from 'react';
import './App.css';
import {
    Panel,
    PanelHeader,
    PanelHeaderBack,
    View,
    Root,
    SimpleCell,
    PanelHeaderClose,
    Header,
    Button, PanelHeaderButton, PanelHeaderContent, Avatar, FixedLayout, Div, Separator, Input, Placeholder
} from "@vkontakte/vkui";
import Typing from "./components/Typing";
import MessagesBlock from "./components/MessagesBlock";
import '@vkontakte/vkui/dist/vkui.css';
import fire from './functions/firebase';
import checkUserExists from './functions/checkUserExist';
import bridge from '@vkontakte/vk-bridge';
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline';
import Icon28PhoneOutline from '@vkontakte/icons/dist/28/phone_outline';
import Icon28AttachOutline from '@vkontakte/icons/dist/28/attach_outline';
import Icon24SmileOutline from '@vkontakte/icons/dist/24/smile_outline';
import Icon28VoiceOutline from '@vkontakte/icons/dist/28/voice_outline';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon24Send from '@vkontakte/icons/dist/24/send';

import $ from 'jquery';


class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            activeView: 'main',
            activePanel: 'main',
            userDataVK: {},
            userData: {},
            farm: {
                activePanel: 'main'
            },
            messages: [],
            typing: false,
            messageValue: ''
        };
        this.openPanel = this.openPanel.bind(this);
        this.updateMessage = this.updateMessage.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
    }
    componentDidMount() {
        bridge.send("VKWebAppInit", {})
            .then(
                r => bridge.send("VKWebAppGetUserInfo", {})
                    .then(
                        z => {
                            this.setState({
                                userDataVK: z
                            });
                            checkUserExists(z.id).then(
                                x => {
                                    fire.firestore().collection('users').doc(z.id.toString()).onSnapshot(listener => {
                                        this.setState({
                                            userData: listener.data(),
                                            messages: listener.data().messages
                                        });
                                        console.log(this.state);
                                    });
                                    setTimeout(() => {this.addMessage('Привет, давно тебя не было в уличных гонках! Заходи!', false)}, 1000);
                                }
                            );
                        }
                    )
            );
    }
    componentWillUnmount() {

    }
    openPanel(panel){
        this.setState({
            activePanel: panel
        })
    }
    openView(view){
        this.setState({
            activeView: view
        })
    }
    updateMessage(e){
        this.setState({
            messageValue: e.target.value
        })
    }
    addMessage(text, sent){
        if(!sent){
            setTimeout(() => {
                this.setState({
                    typing: true
                });
                setTimeout(() => {
                    this.setState(prevState => ({
                        messages: [...prevState.messages, {text: text, sent: sent}],
                        typing: false
                    }))
                }, 2000);
            }, 1000)
        }
        else{
            this.setState(prevState => ({
                messages: [...prevState.messages, {text: text, sent: sent}],
                messageValue: ''
            }));
        }
        fire.firestore().collection('users').doc(this.state.userDataVK.id.toString()).update({messages: this.state.messages});
        $("html, body").animate({ scrollTop: $(document).height() }, "slow");
    }
    sendMessage(){
        this.addMessage(this.state.messageValue, true);
        $('.messageInput').focus();
    }
    render() {
        return (
            <Root activeView={this.state.activeView}>
                <View id="main" activePanel={this.state.activePanel}>
                    <Panel id="main">
                        <PanelHeader
                            separator={false}
                            left={<PanelHeaderButton onClick={() => {window.history.back()}}><Icon28ArrowLeftOutline/></PanelHeaderButton>}
                            right={<PanelHeaderButton><Icon28PhoneOutline style={{color: 'var(--button_muted_foreground)'}}/></PanelHeaderButton>}
                            //style={{boxShadow: '0px 1px 5px 0px rgba(0, 0, 0, 0.3)'}}
                        >
                            <PanelHeaderContent
                                status={<Typing typing={this.state.typing}/>}
                                before={<Avatar size={36} src="https://vk.com/images/camera_100.png" />}
                            >
                                <span style={{fontWeight: "bold"}}>DELETED<Icon16Dropdown style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block", margin: "4px 0 0 3px"}}/></span>
                            </PanelHeaderContent>
                        </PanelHeader>
                        <MessagesBlock data={this.state}/>
                        <FixedLayout style={{backgroundColor: '#FFFFFF'}} vertical="bottom">
                            <Separator/>
                            <Div>
                                <Icon28AttachOutline width={28} height={28} style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block"}}/>
                                <input value={this.state.messageValue} onChange={this.updateMessage} maxLength={32} className="messageInput" placeholder="Сообщение"/>
                                <Icon24SmileOutline width={28} height={28} style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block", margin: "0 5px"}}/>
                                {this.state.messageValue ? (<Icon24Send onClick={this.sendMessage} className="iconChangeable" width={28} height={28} style={{color: 'var(--accent)', display: "inline-block"}}/>) : (<Icon28VoiceOutline className="iconChangeable" width={28} height={28} style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block"}}/>)}
                            </Div>
                        </FixedLayout>
                    </Panel>
                </View>
                <div id="scrollTo"/>
            </Root>
        );
    }
}

export default App;
